.mf-layout__cell {
  display: inline-block;
}
.mf-layout__cell--card {
    padding: 0.625rem;
}
.mf-layout__card-wrapper {
  margin-right: -0.625rem;
  margin-left: -0.625rem;
}
@media only screen and (max-width: 479px) {
.mf-layout__container {
    margin-left: 0rem;
    margin-right: 0rem;
}
.mf-layout__container--model {
      width: 18.75rem;
      margin-left: auto;
      margin-right: auto;
}
.mf-layout__cell {
    width: 50%;
}
}
@media only screen and (min-width: 480px) and (max-width: 767px) {
.mf-layout__container {
    margin-left: 1.25rem;
    margin-right: 1.25rem;
}
.mf-layout__container--model {
      width: 37.5rem;
      margin-left: auto;
      margin-right: auto;
}
.mf-layout__cell {
    width: 50%;
}
}
@media only screen and (min-width: 768px) and (max-width: 1023px) {
