*::-moz-selection {
  background-color: #00b8d4;
  color: white;
}
*::selection {
  background-color: #00b8d4;
  color: white;
}
.mf-tag {
  padding: 0.25rem;
  background-color: #d7d7d7;
  color: #969696;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.mf-cell-card__body {
  border-radius: 0.1875rem;
  background-color: #FFF;
  box-shadow: 0 0 0.3125rem 0 rgba(0, 0, 0, 0.2);
}
.mf-cell-card__thumbnail {
  position: relative;
}
.mf-cell-card__thumbnail:before {
    display: block;
    content: "";
    width: 100%;
    padding-top: 62.5%;
}
.mf-cell-card__thumbnail > .mf-cell-card__thumbnail-content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.mf-cell-card__thumbnail-content {
    border-radius: 0.1875rem 0.1875rem 0 0;
}
.mf-cell-card__overlay {
  cursor: pointer;
}
.mf-cell-card__overlay > .mf-cell-card__ctrls {
    display: inline-block;
    position: absolute;
    top: 0.9375rem;
    right: 0.9375rem;
    opacity: 0;
}
.mf-cell-card__overlay > .mf-cell-card__ctrls > .mf-cell-card-ctrl:not(:first-child) {
      margin-top: 0.625rem;
}
.mf-cell-card__overlay:hover {
    background-color: rgba(255, 255, 255, 0.2);
}
.mf-cell-card__overlay:hover > .mf-cell-card__ctrls {
      opacity: 1;
}
.mf-cell-card__info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  color: #bdbdbd;
  font-size: 0.75rem;
}
.mf-cell-card__info-item {
    max-width: 60%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin: 0.625rem 0 0.625rem 0.625rem;
}
.mf-cell-card__info-item a {
      text-decoration: none;
      out-line: none;
      color: #bdbdbd;
}
.mf-cell-card__info-item--clickable {
      cursor: pointer;
      margin: 0;
      padding: 0.625rem;
}
.mf-cell-card__info-item--clickable.active {
        color: #00b8d4;
}
.mf-cell-card__info-icon {
    margin-right: 0.25rem;
}
.mf-cell-card__info-icon.mf-icon-download {
      cursor: pointer;
}
.mf-cell-card__info-spacer {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}



