.popup__wrapper {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.popup__wrapper.modal {
    background-color: rgba(30, 30, 30, 0.8);
}
.popup__wrapper.fade-enter, .popup__wrapper.fade-leave-active {
    opacity: 0;
}
.popup__wrapper.fade-enter .popup__content, .popup__wrapper.fade-leave-active .popup__content {
      -webkit-transform: translateY(-40px);
              transform: translateY(-40px);
      transition-duration: 250ms;
}
.popup__wrapper.fade-enter-active {
    transition: all 250ms ease-in;
}
.popup__wrapper.fade-enter-active .popup__content {
      transition-duration: 250ms;
}
.popup__wrapper.fade-leave-active {
    transition: all 250ms ease-out;
}
.popup__wrapper .popup__content {
    -ms-flex-item-align: center;
        -ms-grid-row-align: center;
        align-self: center;
    border-radius: 5px;
    background-color: #ffffff;
}
.popup__wrapper .popup__content .title {
      margin-top: 3.125rem;
      margin-bottom: 1.25rem;
      height: 2.0625rem;
      min-width: 34.6875rem;
      width: 100%;
      max-width: 99vw;
      text-align: center;
      font-size: 1.5rem;
      font-weight: 500;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: normal;
      color: #00b8d4;
}
.popup__wrapper .popup__content .content {
      margin-top: 1.25rem;
      margin-left: 2.1875rem;
      margin-right: 2.1875rem;
      margin-bottom: 1.5625rem;
      padding: 1.875rem;
      border-radius: 0.3125rem;
      background-color: #f5f5f5;
      border: solid 1px #eeeeee;
      line-height: 2.14;
      letter-spacing: normal;
      text-align: left;
      color: #616161;
}
.popup__wrapper .popup__content .action-buttons {
      margin: 1.5625rem 2.1875rem;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: reverse;
          -ms-flex-flow: row-reverse nowrap;
              flex-flow: row-reverse nowrap;
}
.popup__wrapper .popup__content .action-buttons .button {
        padding: 0 1.9375rem;
        height: 2.125rem;
        border: none;
}
.popup__wrapper .popup__content .action-buttons .button.link-button {
          color: #bdbdbd;
}



