
.mfb-side-buttons {
  box-sizing: border-box;
  z-index: 10;
  padding: 0 6px;
  font-size: 12px;
  cursor: pointer;
  text-align: center;
  position: relative;
}
.mfb-side-buttons__scrolltop {
    position: fixed;
    right: 30px;
    display: block;
    color: #ffffff;
    font-size: 30px;
    width: 50px;
    height: 50px;
    background-color: rgba(32, 32, 32, 0.6);
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.2);
    border-radius: 50%;
    bottom: 40px;
    padding: 10px;
}
.mfb-side-buttons__scrolltop:visited {
      color: #ffffff;
}
.mfb-side-buttons__scrolltop:hover {
      background-color: #2f2b2b;
}
.mfb-side-buttons__scrolltop:before {
      content: "\E929";
}
.mfb-side-buttons__scrolltop.mfb-icon-to-top {
      line-height: 1.6;
}



