/* Add on var */
.mf-ani-fade-enter-active,
.mf-ani-fade-leave-active {
  transition: opacity 100ms linear;
}
.mf-ani-fade-enter,
.mf-ani-fade-leave-active {
  opacity: 0;
}
.mf-ani-fade--m-enter-active,
.mf-ani-fade--m-leave-active {
  transition: opacity 300ms linear;
}
.mf-ani-fade--m-enter,
.mf-ani-fade--m-leave-active {
  opacity: 0;
}
.mf-ani-fade-in-enter-active {
  transition: opacity 100ms linear;
}
.mf-ani-fade-in-leave-active {
  transition: none;
}
.mf-ani-fade-in-enter,
.mf-ani-fade-in-leave-active {
  opacity: 0;
}
.mf-ani-fade-out-enter-active {
  transition: none;
}
.mf-ani-fade-out-leave-active {
  transition: opacity 100ms linear;
}
.mf-ani-fade-out-enter,
.mf-ani-fade-out-leave-active {
  opacity: 0;
}
