/*location of checkbox */
.mfb-temp .mf-layout__cell--card {
  position: relative;
  /*控制hover样式*/
}
.mfb-temp .mf-layout__cell--card .mf-checkbox--light {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
}
.mfb-temp .mf-layout__cell--card .mf-cell-card:hover + .mf-checkbox--light .mf-checkbox__box {
    border-color: black;
    opacity: 1;
}



