/* Color palette */
/* Add on var */
.mf-image {
  background-size: contain;
  position: relative;
  overflow: hidden;
  background-color: black;
  background-position: center;
}
.mf-image__loader {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    display: block;
}
.mf-image__content {
    position: absolute;
    left: 0;
    top: 0;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    pointer-events: none;
    width: 100%;
    height: 100%;
}



