
.model_download__popup .popup__content .content {
  padding: rem(20) !important;
}
.model_download__popup .download_thumbnail, .model_download__popup .download_info {
  vertical-align: middle;
  height: rem(76.5);
  display: inline-block;
}
.model_download__popup .download_thumbnail {
  width: rem(124);
  border-radius: rem(4);
}
.model_download__popup .download_info {
  margin-left: rem(20);
  max-width: rem(300);
}
.model_download__popup .download_info p {
    margin: rem(8) 0;
    white-space: nowrap;
}
.model_download__popup .download_info p span.model_download__title {
      white-space: nowrap;
      max-width: 75%;
}
.model_download__popup .download_info p > span {
      display: inline-block;
      line-height: 0.9;
}
.model_download__popup .download_info p i.mf-icon-information {
      margin-left: 0.2rem;
      color: #ffffff;
}



